html, body, #root {
    height: 100%;
}

body {
    overflow-y: scroll;

    -ms-overflow-style: none;
    scrollbar-width: none;
}

body::-webkit-scrollbar {
    display: none;
}

body {
    background-color: rgb(248, 255, 148);
}

@media screen and (max-width: 1000px) {
    body {
        background-color: #fff;
    }
}

#Container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
}